	import {getAlbum} from '../../api/api.js';

    export default {
		data() {
			return {
				dynInfo: '',
			}
		},
        methods: {
			getDynInfo() {
				getAlbum().then(data => {
					// console.log(data);
					this.dynInfo = data;
					console.log(this.dynInfo);
				});
			},
			preview(index) {
				let imgUrl = []
				for(let i =0 ;i<this.dynInfo.length;i++){
					imgUrl[i] = this.dynInfo[i].images;
				}
				console.log(imgUrl)
				uni.previewImage({
					current:index,
					urls: imgUrl,
				});
				}
			
        },
		onLoad(option) {
			this.uid = option.uid;
		},
		onShow() {
			this.getDynInfo();
		}
    }
